#main_wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  transition: all 1s;
  z-index: 10;
}
.background_video {
  width: 100%;
  height: 100%;
  background-size: contain;
  position: inherit;
  object-fit: fill;
}

@media all and (max-width: 1024px) {
  #main_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #000;
  }
  .background_video {
    height: auto !important;
  }
}
